'use client';

import { useEffect, useState } from 'react';
import { useTranslation } from '@/components/Providers';
import { useOffline } from '@/hooks/useOffline';
import { useSync } from '@/hooks/useSync';
import { dbManager, AuditData } from '@/lib/indexedDB';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import Link from 'next/link';

export default function Dashboard() {
  const { t } = useTranslation();
  const isOffline = useOffline();
  const { pendingItems } = useSync();
  const [stats, setStats] = useState({
    totalAudits: 0,
    completedToday: 0,
    recentAlerts: 0,
  });
  const [recentAudits, setRecentAudits] = useState<AuditData[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadDashboardData = async () => {
      try {
        const audits = await dbManager.getAllAudits();
        const today = new Date();
        today.setHours(0, 0, 0, 0);

        const completedToday = audits.filter(audit => {
          const auditDate = new Date(audit.date);
          auditDate.setHours(0, 0, 0, 0);
          return auditDate.getTime() === today.getTime() && audit.status === 'completed';
        }).length;

        const recentAlerts = audits.reduce((count, audit) => {
          return count + audit.anomalies.filter(anomaly => 
            anomaly.severity === 'high' || anomaly.severity === 'critical'
          ).length;
        }, 0);

        setStats({
          totalAudits: audits.length,
          completedToday,
          recentAlerts,
        });

        // Get 5 most recent audits
        const sortedAudits = audits
          .sort((a, b) => new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime())
          .slice(0, 5);
        
        setRecentAudits(sortedAudits);
      } catch (error) {
        console.error('Error loading dashboard data:', error);
      } finally {
        setLoading(false);
      }
    };

    loadDashboardData();
  }, []);

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold text-foreground">{t('dashboard.title')}</h1>
        <p className="text-muted-foreground mt-2">{t('dashboard.welcome')}</p>
      </div>

      {/* Status Banner */}
      {isOffline && (
        <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
          <div className="flex items-center gap-2">
            <div className="w-2 h-2 bg-yellow-500 rounded-full"></div>
            <span className="text-yellow-800 font-medium">
              Mode hors-ligne activ√© - Les donn√©es seront synchronis√©es automatiquement
            </span>
          </div>
        </div>
      )}

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              {t('dashboard.stats.totalAudits')}
            </CardTitle>
            <span className="text-2xl">üìä</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalAudits}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              {t('dashboard.stats.pendingSync')}
            </CardTitle>
            <span className="text-2xl">üîÑ</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{pendingItems}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              {t('dashboard.stats.recentAlerts')}
            </CardTitle>
            <span className="text-2xl">‚ö†Ô∏è</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.recentAlerts}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              {t('dashboard.stats.completedToday')}
            </CardTitle>
            <span className="text-2xl">‚úÖ</span>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.completedToday}</div>
          </CardContent>
        </Card>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Quick Actions */}
        <Card>
          <CardHeader>
            <CardTitle>{t('dashboard.quickActions.title')}</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <Link href="/audit/capture">
              <Button className="w-full justify-start" size="lg">
                <span className="mr-2">üì∑</span>
                {t('dashboard.quickActions.newAudit')}
              </Button>
            </Link>
            <Link href="/audit/report">
              <Button variant="outline" className="w-full justify-start" size="lg">
                <span className="mr-2">üìÑ</span>
                {t('dashboard.quickActions.viewReports')}
              </Button>
            </Link>
            <Link href="/team-chat">
              <Button variant="outline" className="w-full justify-start" size="lg">
                <span className="mr-2">üí¨</span>
                {t('dashboard.quickActions.teamChat')}
              </Button>
            </Link>
          </CardContent>
        </Card>

        {/* Recent Audits */}
        <Card>
          <CardHeader>
            <CardTitle>Audits r√©cents</CardTitle>
          </CardHeader>
          <CardContent>
            {recentAudits.length === 0 ? (
              <p className="text-muted-foreground text-center py-4">
                Aucun audit r√©cent
              </p>
            ) : (
              <div className="space-y-3">
                {recentAudits.map((audit) => (
                  <div
                    key={audit.id}
                    className="flex items-center justify-between p-3 bg-muted/50 rounded-lg"
                  >
                    <div>
                      <h4 className="font-medium">{audit.title}</h4>
                      <p className="text-sm text-muted-foreground">
                        {audit.client} ‚Ä¢ {new Date(audit.date).toLocaleDateString()}
                      </p>
                    </div>
                    <div className="flex items-center gap-2">
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                        audit.status === 'completed' 
                          ? 'bg-green-100 text-green-800'
                          : audit.status === 'synced'
                          ? 'bg-blue-100 text-blue-800'
                          : 'bg-yellow-100 text-yellow-800'
                      }`}>
                        {audit.status}
                      </span>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
